import { v3 } from "cc";
import { v2 } from "cc";

export class Vector3 {
    x: number = 0;
    y: number = 0;
    z: number = 0;

    set(x?: number, y?: number, z?: number) {
        if (x !== undefined) {
            this.x = x;
        }

        if (y !== undefined) {
            this.y = y;
        }

        if (z !== undefined) {
            this.z = z;
        }

    }

    add(vec3: Vector3) {
        this.x += vec3.x;
        this.y += vec3.y;
        this.z += vec3.z;

    }

    multiply(val: number) {
        this.x *= val;
        this.y *= val;
        this.z *= val;
    }

    resetToZero() {
        this.x = 0;
        this.y = 0;
        this.z = 0;
    }
}
